import React, { useState } from "react";
import Button from "@mui/material/Button";

const RateForm = (props) => {
  let latlon = props.location;
  // state to hold form data
  const [formInfo, setFormInfo] = useState({
    cleanliness: "1",
    reusableMaterial: "1",
    traffic: "1",
    recycle: "1",
    plants: "1",
  });

  // state to change state of different form inputs
  function handleChange(event) {
    const { name, value } = event.target;

    setFormInfo((prev) => {
      if (name === "cleanliness") {
        return {
          ...prev,
          [name]: value,
        };
      } else if (name === "reusableMaterial") {
        return {
          ...prev,
          [name]: value,
        };
      } else if (name === "traffic") {
        return {
          ...prev,
          [name]: value,
        };
      } else if (name === "recycle") {
        return {
          ...prev,
          [name]: value,
        };
      } else if (name === "plants") {
        return {
          ...prev,
          [name]: value,
        };
      }
    });
  }

  //display form info after form is submitted
  function info() {
    console.log(formInfo);
  }

  return (
    <div>
      <form action="../../post" method="post">
        <input
          hidden
          type="text"
          name="location"
          value={JSON.stringify(latlon)}
          readOnly
        />
        <label>Cleanliness</label>
        <input
          type="range"
          min="1"
          max="5"
          name="cleanliness"
          onChange={handleChange}
          value={formInfo.cleanliness}
        />
        <br />
        <label>Reusable Material</label>
        <input
          type="range"
          min="1"
          max="5"
          name="reusableMaterial"
          onChange={handleChange}
          value={formInfo.reusableMaterial}
        />
        <br />
        <label>Traffic</label>
        <input
          type="range"
          min="1"
          max="5"
          name="traffic"
          onChange={handleChange}
          value={formInfo.traffic}
        />
        <br />
        <label>Recycle Options</label>
        <input
          type="range"
          min="1"
          max="5"
          name="recycle"
          onChange={handleChange}
          value={formInfo.recycle}
        />
        <br />
        <label htmlFor="plants">Plant Life</label>
        <input
          type="range"
          min="1"
          max="5"
          name="plants"
          onChange={handleChange}
          value={formInfo.plants}
        />
        <br />
        <Button
          variant="contained"
          type="submit"
          onClick={info}
          style={{ float: "right" }}
        >
          Submit
        </Button>
      </form>
    </div>
  );
};

export default RateForm;
