import React from "react";
import Paper from "@mui/material/Paper";

//about page
const About = () => {
  return (
    <div>
      <h2>How it Works</h2>
      <Paper
        className="paper"
        style={{ textAlign: "left", "background-color": "#c3ecb2" }}
        elevation={3}
      >
        <h3>Map Navigation:</h3>
        <ul>
          <li>
            <b>Move Around</b> - To move around the map, click on the left mouse
            button or press down on the track pad, then drag your mouse or
            finger toward the opposite direction of the area you want to view.
          </li>
          <li>
            <b>Zoom</b> - Click on the plus button on the botom right corner of
            the screen to zoom in and click the minus button below the plus
            button to zoom out. If you are using a touchpad or touschscreen, you
            can pinch or spread your fingers to zoom in and out
          </li>
          <li>
            <b>Full Screen</b> - Click on the square border button on the top
            right corner of the map to view the map in full screen. Press the
            "esc" button to escape full screen mode
          </li>
          <li>
            <b>Views</b> - Click the map button on the top left corner of the
            application to have a roadmap view. Click on the satelite button
            beside the map button to have a satelite view of the application.
            Click and drag the yellow pegman on the button right side of the
            application to get a street view of a particular location
          </li>
        </ul>
      </Paper>
      <Paper
        className="paper"
        elevation={3}
        style={{ textAlign: "left", "background-color": "#c3ecb2" }}
      >
        <h3>Markers:</h3>
        <ul>
          <li>
            <b>Default Markers</b> -These markers are colorful and have an image
            of the the location's type inside. Click on these markers to open a
            infoWIndow that has the location's name, address, and a link to view
            the location on Google Maps.
          </li>
          <li>
            <b>Red Markers</b> - These markers are previous rating submitions.
            click on these markers to view the overall rating of the location.
            click on the "Add Rating" button to add your own rating to the
            location.
          </li>
          <li>
            <b>Green Marker</b> - The green marker allows you to add a rating to
            a new location. Click on the location that you want to add and the
            green marker will be placed on the location that you clicked. Click
            on the green marker, then press the "Add Rating button in the
            infoWindow to add a new rating.
          </li>
        </ul>
      </Paper>
      <Paper
        className="paper"
        elevation={3}
        style={{ textAlign: "left", "background-color": "#c3ecb2" }}
      >
        <h3>Ratings:</h3>
        <ul>
          <li>
            <b>Cleanliness</b> - How clean is the location's surrounding
            element.
          </li>
          <li>
            <b>Reusable Material</b> - The amount of reuseable material the
            establishment uses.
          </li>
          <li>
            <b>Recycle Opportunities</b> - Access to recycling bins, and other
            bins to seperate waste from reusable materials.
          </li>
          <li>
            <b>Traffic</b> - The amount of people who visit the location.
          </li>
          <li>
            <b>Plant Life</b> - How much plant life, shrubbery, and flora
            surrounds the location.
          </li>
        </ul>
      </Paper>
    </div>
  );
};

export default About;
