import React, { useState, useEffect } from "react";
import {
  GoogleMap,
  LoadScript,
  Marker,
  InfoWindow,
} from "@react-google-maps/api";
import RateForm from "./RateForm";
import axios from "axios";
import Button from "@mui/material/Button";
import Rating from "@mui/material/Rating";

const MapContainer = () => {
  // state of selected marker
  const [selected, setSelected] = useState({});

  //change state of selected marker
  const onSelect = (item) => {
    setSelected(item);
  };
  // state of form on marker's InfoWindow is open or not
  const [formOpen, setFormOpen] = useState(true);

  //holds locations from db call
  const [DBLocations, setDBLocations] = useState([]);

  // opens and closes form on InfoWindow
  const showForm = () => {
    setFormOpen(!formOpen);
  };

  useEffect(() => {
    axios.post("../../ratingsDB").then((res) => {
      const locations = res.data;
      console.log(locations);
      setDBLocations(locations);
    });
  }, []);

  // style the google map
  const mapStyles = {
    height: "100vh",
    width: "100%",
  };

  //set the default location of map
  const [defaultCenter, setDefaultCenter] = useState({
    lat: 31.5785,
    lng: -84.1557,
  });
  //state to hold new marker location
  const [marker, setMarker] = useState({
    lat: 31.570602578009236,
    lng: -84.15707329101562,
  });

  //get coordinates of area that was clicked on map
  const getCoordinates = (e) => {
    const lat = e.latLng.lat();
    const lng = e.latLng.lng();
    setMarker({ lat, lng });

    console.log(marker);
  };

  //state to allow new form window from open to close
  const [windowOpen, setWindowOpen] = useState(false);

  //open and closes new form infoWindow
  const newWindow = () => {
    setWindowOpen(!windowOpen);
  };

  // turns array of strings into numbers then gets average of array
  const average = (array) =>
    array.map((item) => Number(item)).reduce((a, b) => a + b) / array.length;

  return (
    <LoadScript googleMapsApiKey="AIzaSyBwWcYn8syGC5_JKbx7WN21Vs6cPVb5kOo">
      <GoogleMap
        mapContainerStyle={mapStyles}
        zoom={13}
        center={defaultCenter}
        onClick={(e) => getCoordinates(e)}
      >
        {/* new Marker */}
        {
          <Marker
            icon="http://maps.google.com/mapfiles/ms/icons/green-dot.png"
            position={marker}
            onClick={() => newWindow()}
          />
        }

        {/* window for new marker */}
        {windowOpen === true ? (
          <InfoWindow
            position={marker}
            clickable={true}
            onCloseClick={() => setWindowOpen(false)}
          >
            <div>
              {formOpen ? (
                <div>
                  <p>No Rating</p>
                </div>
              ) : (
                <RateForm location={marker} />
              )}{" "}
              <Button
                variant="contained"
                onClick={showForm}
                style={{ float: "left" }}
              >
                <div>{formOpen ? "Add Rating" : "Cancel"}</div>
              </Button>
            </div>
          </InfoWindow>
        ) : null}

        {/* markers for set locations */}
        {DBLocations.map((item) => {
          return (
            <Marker
              key={item._id}
              position={JSON.parse(item.location)}
              onClick={() => onSelect(item)}
            />
          );
        })}

        {/* info window for set location */}
        {selected.location && (
          <InfoWindow
            position={JSON.parse(selected.location)}
            clickable={true}
            onCloseClick={() => setSelected({})}
          >
            <div>
              {formOpen ? (
                <div className="description">
                  <p>
                    <b>Location ID: {selected._id}</b>
                  </p>
                  <p>
                    Cleanliness:{" "}
                    <Rating
                      className="stars"
                      name="half-rating"
                      defaultValue={average(selected.cleanliness).toFixed(1)}
                      precision={0.5}
                      readOnly
                    />
                  </p>
                  <p>
                    Reusable Material:{" "}
                    <Rating
                      className="stars"
                      name="half-rating"
                      defaultValue={average(selected.reusableMaterial).toFixed(
                        1
                      )}
                      precision={0.5}
                      readOnly
                    />
                  </p>
                  <p>
                    Traffic:{" "}
                    <Rating
                      className="stars"
                      name="half-rating"
                      defaultValue={average(selected.traffic).toFixed(1)}
                      precision={0.5}
                      readOnly
                    />
                  </p>
                  <p>
                    Recycle Opportunities:{" "}
                    <Rating
                      className="stars"
                      name="half-rating"
                      defaultValue={average(selected.recycle).toFixed(1)}
                      precision={0.5}
                      readOnly
                    />
                  </p>
                  <p>
                    Plants:{" "}
                    <Rating
                      className="stars"
                      name="half-rating"
                      defaultValue={average(selected.plants).toFixed(1)}
                      precision={0.5}
                      readOnly
                    />
                  </p>
                </div>
              ) : (
                <div>
                  <RateForm location={JSON.parse(selected.location)} />
                </div>
              )}
              <Button
                variant="contained"
                onClick={showForm}
                style={{ float: "left" }}
              >
                <div>{formOpen ? "Add Rating" : "Cancel"}</div>
              </Button>
            </div>
          </InfoWindow>
        )}
      </GoogleMap>
    </LoadScript>
  );
};

export default MapContainer;
