import React from "react";
import "../App.css";
import MapContainer from "./Map";
import ResponsiveAppBar from "./NavBar";
import { useSelector } from "react-redux";
import About from "./About";

function App() {
  //gets to state of page (either true or false)
  const pageSlice = useSelector((state) => state.page.value);
  console.log(pageSlice);
  return (
    <div className="App">
      <ResponsiveAppBar />

      {/* If page is false map will be opn but if page is false, about page will be opened. */}
      {pageSlice === "map" ? <MapContainer /> : <About />}
    </div>
  );
}

export default App;
