import { createSlice } from "@reduxjs/toolkit";
//page hold start (true or false) to open and close map and about page
export const pageSlice = createSlice({
  name: "pageOpen",
  initialState: { value: "map" },
  reducers: {
    about: (state) => {
      state.value = "about";
    },
    map: (state) => {
      state.value = "map";
    },
  },
});

export const { map, about } = pageSlice.actions;

export default pageSlice.reducer;
